.home_image{
    width: 100%;
    position: relative;

}
.home_image img{
    width: 100%;
    object-fit: cover;
}
.home_image::after{
    content: "We build your dream Home!";
    /* font-family: "Font Awesome 6 Free"; */
    width: 100%;
    height: 50px;
    font-weight: bold;
    font-size: 30px;
    text-align: center;     
    padding-top: 4px;
    color: black;
    background-color: orange;
    text-transform: uppercase;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
@media screen and (max-width: 576px)  {
    .home_image::after{
        font-size: 17px;
        height: 30px;
        
    }
}
@media screen and (min-width: 576px) and (max-width:768px){
    .home_image::after{
        font-size: 20px;
        height: 40px;
        
    }
}